dex=\"-1\"\n       @keyup.up=\"minusIndex\"\n       @keyup.down=\"plusIndex\"\n       @keyup.enter.prevent=\"select\"\n       @keyup.delete=\"popTag\"\n       @focus=\"onFocus\">\n    <tag v-for=\"(tagItem, index) in tagsToShow\"\n         :key=\"index\"\n         :tag=\"tagItem\"\n         :index=\"index\"\n         @remove-tag=\"removeTag\">\n    </tag>\n    <input type=\"text\"\n           v-model=\"keyword\"\n           @focus=\"onFocus\"\n           @blur=\"stopHandleInput\"\n           @input=\"input\"\n           ref=\"input\"\n           style=\"float: left;\"\n           :style=\"'width:' + tmpInput.length + 1 + 'em;'\"/>\n    <ul v-show=\"items.length && keyword && searching\" class=\"search-result-container\">\n      <li v-for=\"(item, index) in items\"\n          :key=\"index\"\n          class=\"search-result-item\"\n          @click=\"selectItem(index)\"\n          :class=\"{ highlight: currentIndex === index }\">\n        {{ item[labelKey] }}\n      </li>\n    </ul>\n    <ul class=\"search-result-container\" v-show=\"showNoResult()\">\n      <li class=\"search-result-item text-danger\">没有结果，请更换搜索关键词</li>\n    </ul>\n  </div>\n</template>\n\n<script>\nconst inArray = (item, arr) => arr.indexOf(item) !== -1;\n\nimport tag from './tag';\n\n/*\n * v-model: 标签列表\n * getItems: 关键词变化时调用的函数，参数为关键词，返回Promise\n * labelKey: 用于显示的属性名\n */\n\nexport default {\n  props: {\n    value: {\n      type: Array,\n      required: true\n    },\n    getItems: {\n      type: Function,\n      required: true\n    },\n    labelKey: {\n      type: String,\n      default: 'label'\n    }\n  },\n  components: {\n    tag\n  },\n  data() {\n    return {\n      items: [],\n      currentIndex: 0,\n      searching: false,\n      requestApi: false,\n      keyword: '',\n      prevKeyword: '',\n      tmpInput: '',\n      prevTmpInput: ''\n    };\n  },\n  computed: {\n    itemAmount() {\n      return this.items.length;\n    },\n    tagsToShow() {\n      return this.value.map((item) => item[this.labelKey]);\n    }\n  },\n  watch: {\n    keyword(v, oldV) {\n      this.prevKeyword = oldV;\n\n      if ( v ) {\n        this.search();\n      }\n    },\n    tmpInput(v, oldV) {\n      console.log('tmpInput watcher', oldV);\n      // 用于判断回删tag\n      this.prevTmpInput = oldV;\n    }\n  },\n  methods: {\n    inArray,\n    input(e) {\n      this.tmpInput = e.target.value;\n    },\n    onFocus() {\n      this.$refs.input.focus();\n      if ( this.keyword ) {\n        this.search();\n      }\n    },\n    search() {\n      this.searching = true;\n      this.requestApi = true;\n\n      // 如果两次关键词相同，不调用接口，适用于已经输入关键词后的二次获取焦点\n      if ( this.keyword === this.prevKeyword ) {\n        return;\n      }\n\n      Promise.resolve(this.getItems(this.keyword))\n        .then((items) => {\n          this.requestApi = false;\n          this.items = items;\n        })\n        .catch((err) => {\n          this.requestApi = false;\n          console.error(err);\n        });\n    },\n    removeTag(index) {\n      this.value.splice(index, 1);\n    },\n    popTag() {\n      if ( !this.prevTmpInput && !this.tmpInput ) {\n        this.value.pop();\n        return;\n      }\n      if ( this.prevTmpInput && !this.tmpInput ) {\n        this.prevTmpInput = '';\n        return;\n      }\n    },\n    select() {\n      const item = this.items[this.currentIndex];\n\n      if ( !inArray(item, this.value) ) {\n        this.value.push(item);\n      }\n\n      this.keyword = '';\n      this.currentIndex = 0;\n      this.searching = false;\n      this.tmpInput = '';\n      setTimeout(() => {\n        this.prevTmpInput = '';\n      });\n    },\n    plusIndex() {\n      this.currentIndex = (this.currentIndex + 1) % this.itemAmount;\n    },\n    minusIndex() {\n      this.currentIndex = (this.currentIndex - 1 + this.itemAmount) % this.itemAmount;\n    },\n    selectItem(index) {\n      this.currentIndex = index;\n\n      this.select();\n    },\n    stopHandleInput() {\n      const delay = 200;    // 增加200ms的延时，以应对点击选择的情况，因为这种情况下input会首先失去焦点，触发这个事件\n\n      setTimeout(() => {\n        this.searching = false;\n        if ( this.keyword === '' ) {\n          this.selectedItem = '';\n        }\n      }, delay);\n    },\n    showNoResult() {\n      return this.keyword\n        && !this.requestApi\n        && this.items.length === 0;\n    }\n  }\n};\n</script>\n<style scoped>\nul {\n    padding: 0;\n    margin: 0;\n}\n\n.tag-input-container {\n    position: relative;\n    z-index: 1000;\n    user-select: none;\n    box-sizing: border-box;\n    padding: 0 5px 3px 5px;\n    border-radius: 4px;\n    border: 1px solid #d9d9d9;\n    outline: none;\n    background-color: #fff;\n    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);\n    min-width: 200px;\n}\n\n.tag-input-container::after {\n    content: '';\n    display: block;\n    clear: both;\n}\n\n.tag-input-container:focus {\n    outline: none;\n}\n\n.tag-input-container input {\n    border: none;\n    outline: none;\n    line-height: 20px;\n    height: 20px;\n    padding: 0;\n    margin-top: 3px;\n    min-width: 1em;\n}\n\n.search-result-container {\n    margin: 10px 0;\n    border: 1px solid #66afe9;\n    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);\n    border-radius: 5px;\n    background-color: white;\n    cursor: pointer;\n    list-style: none;\n    position: absolute;\n    left: 0;\n    width: 100%;\n    overflow: auto;\n}\n\n.search-result-item {\n    padding: 5px 10px;\n}\n.search-result-item.highlight {\n    background: #eee;\n}\n</style>\n\n"],"sourceRoot":"webpack://"}]);

